<template>
  <div class="content-wrapper">
    <div class="card-body">
      <div>
        <span style="float: left; margin-top: 10px; margin-right: 5px"
          >搜索</span
        >
      </div>
      <div style="float: left">
        <el-select clearable v-model="input" placeholder="活动类型">
          <el-option label="自习" value="自习"></el-option>
          <el-option label="锻炼" value="锻炼"></el-option>
          <el-option label="外出" value="外出"></el-option>
          <el-option label="班会" value="班会"></el-option>
          <el-option label="小组作业" value="小组作业"></el-option>
          <el-option label="创新创业" value="创新创业"></el-option>
          <el-option label="聚餐" value="聚餐"></el-option>
        </el-select>
      </div>
      <el-button
        type="primary"
        size="small"
        icon="el-icon-search"
        @click="searchact()"
        style="float: left; margin-left: 10px; margin-top: 4px"
        >搜索</el-button
      >

      <el-table :data="showData" height="500" stripe style="width: 100%">
        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          label="id"
          prop="id"
        >
        </el-table-column>
        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          prop="person"
          label="创建人"
        >
          <template slot-scope="scope">
            <span>{{ scope.row.personal }} <br /></span>
          </template>
        </el-table-column>
        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          label="类别"
          prop="name"
        >
          <template slot-scope="scope">
            <span>{{ str[scope.row.multiple] }} <br /></span>
          </template>
        </el-table-column>
        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          label="活动"
          prop="name"
        >
        </el-table-column>
        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          label="冲突"
          prop="name"
        >
          <template slot-scope="scope">
            <span>{{ constr[scope.row.conflict] }} <br /></span>
          </template>
        </el-table-column>

        <el-table-column align="center" label="time">
          <template slot-scope="scope">
            <span
              >{{ new Date(scope.row.time[0]).toLocaleString().slice(0, -3) }}
              <br
            /></span>
            <span
              >{{ new Date(scope.row.time[1]).toLocaleString().slice(0, -3) }}
              <br
            /></span>
          </template>
        </el-table-column>

        <!-- <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          prop="no"
          label="url"
        >
          <template slot-scope="scope">
            <div style="height: 40px; line-height: 40px">
              <span>
                <el-link type="primary" @click="view(scope.row)">详情</el-link>
              </span>

            </div>
          </template>
        </el-table-column> -->

        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          label="edit"
          width="200"
        >
          <template slot-scope="scope">
            <el-button
              size="small"
              type="danger"
              icon="el-icon-delete"
              @click="remove(scope.row)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>

      <el-button type="success" size="small" @click="dialogFormVisible = true"
        >添加活动</el-button
      >

      <el-dialog title="新建活动" :visible.sync="dialogFormVisible">
        <el-form :model="form">
          <el-form-item label="活动名" :label-width="formLabelWidth">
            <el-select v-model="form.name" placeholder="活动类型">
              <el-option label="自习" value="自习"></el-option>
              <el-option label="锻炼" value="锻炼"></el-option>
              <el-option label="外出" value="外出"></el-option>
              <el-option label="班会" value="班会"></el-option>
              <el-option label="小组作业" value="小组作业"></el-option>
              <el-option label="创新创业" value="创新创业"></el-option>
              <el-option label="聚餐" value="聚餐"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="活动类型" :label-width="formLabelWidth">
            <el-radio v-model="multiple" label="0">个人活动</el-radio>
            <el-radio v-model="multiple" label="1">集体活动</el-radio>
          </el-form-item>
          <el-form-item label="活动时间" :label-width="formLabelWidth">
            <div class="block">
              <el-date-picker
                v-model="value1"
                type="date"
                placeholder="选择日期"
              >
              </el-date-picker>
              <el-time-picker
                is-range
                v-model="value2"
                range-separator="至"
                start-placeholder="开始时间"
                end-placeholder="结束时间"
                placeholder="选择时间范围"
              >
              </el-time-picker>
            </div>
          </el-form-item>
          <!-- <el-form-item label="是否为个人活动" :label-width="formLabelWidth">
            <el-select v-model="form.personal" placeholder="是否为个人活动">
              <el-option label="是" value="是"></el-option>
              <el-option label="否" value="否"></el-option>
            </el-select>
          </el-form-item> -->

          <!-- <el-form-item label="其他参与人" :label-width="formLabelWidth">
            <el-input
              v-model="form.friends"
              placeholder="输入学号，空格分隔，没有则不填"
            ></el-input>
          </el-form-item> -->
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormVisible = false">取 消</el-button>
          <el-button type="primary" @click="add">确 定</el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      sid: JSON.parse(sessionStorage.getItem("user")),
      showData: [],
      tableData: [
        // {
        //   name: "聚餐",
        //   id: 0,
        //   personal: 2020211523,
        //   time: ["2022-05-06-12-0-0", "2022-05-06-12-0-0"],
        //   friends: [2020211523, 2020211522],
        // },
      ],
      dialogTableVisible: false,
      dialogFormVisible: false,
      form: {
        id: "",
        name: "",
        personal: JSON.parse(sessionStorage.getItem("user")),
        multiple: "",
        // friends: "",
        time: ["", ""],
        conflict: 0,
        // delivery: false,
        // type: [],
        // resource: "",
        // desc: "",
      },
      formLabelWidth: "120px",
      realtime: new Date(),
      str: ["个人活动", "集体活动"],
      constr: ["有冲突", "无冲突"],
      value1: "",
      value2: "",
      input: "",
      multiple: "",
    };
  },
  // computed: {
  //   caltime() {
  //     return this.value1 + this.value2;
  //   },
  // },
  methods: {
    searchact() {
      if (this.input === "") {
        this.showData = this.tableData;
      } else {
        this.showData = [];
        for (let i = 0; i < this.tableData.length; i++) {
          if (this.tableData[i].name === this.input) {
            this.showData.push(this.tableData[i]);
          }
        }
      }
    },
    add: function () {
      //校验并发送数据
      if (
        this.form.name === "" ||
        this.value1 === "" ||
        this.value2[0] === "" ||
        this.value2[1] === "" ||
        this.multiple == ""
      ) {
        this.$message({
          type: "info",
          message: "请补全信息",
        });
        return;
      }
      let t = this.value1;
      let t1 = this.value2[0];
      let t2 = this.value2[1];
      this.form.time[0] = new Date(
        t.toLocaleDateString() + " " + t1.toLocaleTimeString()
      );
      this.form.time[1] = new Date(
        t.toLocaleDateString() + " " + t2.toLocaleTimeString()
      );
      console.log(this.form);
      this.form.multiple = parseInt(this.multiple);
      let thats = this;
      axios
        .post("http://localhost:3000/act/add", thats.form)

        .then(function (res) {
          if (res.data.able === 1) {
            thats.tableData = res.data.act;
            thats.$message({
              type: "success",
              message: "添加成功!",
            });
          } else {
            thats.tableData = res.data.act;
            thats.$message({
              type: "info",
              message: "发生冲突，请检查。",
            });
          }
          thats.showData = thats.tableData;
        })
        .catch(function (error) {
          console.log(error);
          thats.$message({
            type: "error",
            message: "网络错误，请检查网络。",
          });
        });
      this.showData = this.tableData;
      this.dialogFormVisible = false;
    },
    // edit: function (item) {
    //   this.$router.push({
    //     name: "EditCourse",
    //     params: {
    //       id: item.id,
    //     },
    //   });
    //   console.log(item);
    // },
    remove: function (index) {
      console.log(index.id);
      if (index.personal != this.sid) {
        this.$message({
          type: "info",
          message: "你不是活动创建者，无法删除。",
        });
      } else {
        let that = this;
        let cid = index.id;
        axios
          .post("http://localhost:3000/act/remove", {
            sid: that.sid,
            aid: cid,
          })
          .then((res) => {
            that.tableData = res.data;
            that.showData = that.tableData;
          })
          .catch((err) => {
            console.log(err);
            that.$message({
              type: "error",
              message: "网络问题，请检查网络!",
            });
          });
      }
      
      // this.tableData.splice(index, 1);
    },
    view: function (item) {
      this.$router.push({
        name: "ViewCourse",
        params: {
          id: item.id,
        },
      });
      console.log(item);
    },
  },
  beforeCreate() {},
  mounted() {
    if (this.sid == null)
      this.$message({
        type: "error",
        message: "请重新登录。",
      });
    let thats = this;
    this.sid = JSON.parse(sessionStorage.getItem("user"));
    axios
      .post("http://localhost:3000/act", { sid: thats.sid })

      .then((res) => {
        console.log(res.status);
        thats.tableData = res.data;
        thats.showData = thats.tableData;
      })
      .catch((error) => {
        console.log(error);
        this.$message({
          type: "error",
          message: "网络问题，请检查网络!",
        });
      });
  },
};
</script>

<style scoped>
.content-wrapper {
  padding: 10px;
}

.button {
  float: left;
  margin: 5px;
}
</style>