<template>
  <div class="content-wrapper">
    <div class="card-body">
      <el-table
        :data="tableData"
        border
        stripe
        style="width: 100%"
        height="500"
        v-if="load"
      >
        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          prop="enddate"
          label="时间"
          width="160"
        >
          <template slot-scope="scope">
            <div style="height: 40px; line-height: 40px">
              <span>{{ scope.row.time1 + ":" + scope.row.time2 }}</span>
            </div>
          </template>
        </el-table-column>

        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          prop="enddate"
          label="星期一"
        >
          <template slot-scope="scope">
            <div style="height: 40px; line-height: 40px">
              <el-link
                :type="typecolor[scope.row.t[0].work]"
                @click="view(scope.row.t[0])"
                >{{ scope.row.t[0].name }}</el-link
              >
            </div>
          </template>
        </el-table-column>

        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          prop="enddate"
          label="星期二"
        >
          <template slot-scope="scope">
            <div style="height: 40px; line-height: 40px">
              <el-link
                :type="typecolor[scope.row.t[1].work]"
                @click="view(scope.row.t[1])"
                >{{ scope.row.t[1].name }}</el-link
              >
            </div>
          </template>
        </el-table-column>

        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          prop="enddate"
          label="星期三"
        >
          <template slot-scope="scope">
            <div style="height: 40px; line-height: 40px">
              <el-link
                :type="typecolor[scope.row.t[2].work]"
                @click="view(scope.row.t[2])"
                >{{ scope.row.t[2].name }}</el-link
              >
            </div>
          </template>
        </el-table-column>

        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          prop="enddate"
          label="星期四"
        >
          <template slot-scope="scope">
            <div style="height: 40px; line-height: 40px">
              <el-link
                :type="typecolor[scope.row.t[3].work]"
                @click="view(scope.row.t[3])"
                >{{ scope.row.t[3].name }}</el-link
              >
            </div>
          </template>
        </el-table-column>

        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          prop="enddate"
          label="星期五"
        >
          <template slot-scope="scope">
            <div style="height: 40px; line-height: 40px">
              <el-link
                :type="typecolor[scope.row.t[4].work]"
                @click="view(scope.row.t[4])"
                >{{ scope.row.t[4].name }}</el-link
              >
            </div>
          </template>
        </el-table-column>

        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          prop="enddate"
          label="星期六"
        >
          <template slot-scope="scope">
            <div style="height: 40px; line-height: 40px">
              <el-link
                :type="typecolor[scope.row.t[5].work]"
                @click="view(scope.row.t[5])"
                >{{ scope.row.t[5].name }}</el-link
              >
            </div>
          </template>
        </el-table-column>

        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          prop="enddate"
          label="星期日"
        >
          <template slot-scope="scope">
            <div style="height: 40px; line-height: 40px">
              <el-link
                :type="typecolor[scope.row.t[6].work]"
                @click="view(scope.row.t[6])"
                >{{ scope.row.t[6].name }}</el-link
              >
            </div>
          </template>
        </el-table-column>
      </el-table>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      typecolor: ["info", "danger"],
      stu: "",
      tableData: [
        {
          time1: "08:00",
          time2: "08:45",
          t: new Array(7).fill(""),
        },
        {
          time1: "08:50",
          time2: "09:35",
          t: new Array(7).fill(""),
        },
        {
          time1: "09:50",
          time2: "10:35",
          t: new Array(7).fill(""),
        },
        {
          time1: "10:40",
          time2: "11:25",
          t: new Array(7).fill(""),
        },
        {
          time1: "11:30",
          time2: "12:15",
          t: new Array(7).fill(""),
        },
        {
          time1: "13:00",
          time2: "13:45",
          t: new Array(7).fill(""),
        },
        {
          time1: "13:50",
          time2: "14:35",
          t: new Array(7).fill(""),
        },
        {
          time1: "14:45",
          time2: "15:30",
          t: new Array(7).fill(""),
        },
        {
          time1: "15:40",
          time2: "16:25",
          t: new Array(7).fill(""),
        },
        {
          time1: "16:35",
          time2: "17:20",
          t: new Array(7).fill(""),
        },
        {
          time1: "17:25",
          time2: "18:10",
          t: new Array(7).fill(""),
        },
        {
          time1: "18:30",
          time2: "19:15",
          t: new Array(7).fill(""),
        },
        {
          time1: "19:20",
          time2: "20:05",
          t: new Array(7).fill(""),
        },
        {
          time1: "20:10",
          time2: "20:55",
          t: new Array(7).fill(""),
        },
      ],
      sid: "",
      courses: [],
      load: false,
    };
  },

  methods: {
    in(coursetime, tab) {
      let ch1, ch2, cm1, cm2;
      let th1, th2, tm1, tm2;
      let tmp;
      tmp = coursetime[0].split(":");
      ch1 = parseInt(tmp[0]);
      cm1 = parseInt(tmp[1]);
      tmp = coursetime[1].split(":");
      ch2 = parseInt(tmp[0]);
      cm2 = parseInt(tmp[1]);
      tmp = tab.time1.split(":");
      th1 = parseInt(tmp[0]);
      tm1 = parseInt(tmp[1]);
      tmp = tab.time2.split(":");
      th2 = parseInt(tmp[0]);
      tm2 = parseInt(tmp[1]);
      if (th1 * 60 + tm1 >= ch1 * 60 + cm1 && th2 * 60 + tm2 <= ch2 * 60 + cm2)
        return 1;
      else return 0;
    },
    view: function (item) {
      this.$router.push({
        name: "ViewCourse",
        params: {
          id: item.id,
          sid: this.sid,
        },
      });
      console.log(item);
    },
    generate() {
      for (let i = 0; i < this.courses.length; i++) {
        for (let j = 0; j < 7; j++) {
          if (this.courses[i].week[j] === true) {
            for (let k = 0; k < this.tableData.length; k++) {
              console.log(this.courses[i].time[j], this.tableData[k]);
              if (this.in(this.courses[i].time[j], this.tableData[k])) {
                this.tableData[k].t[j] = this.courses[i];
              }
            }
          }
        }
      }
      this.load = true;
    },
  },
  mounted() {
    this.sid = JSON.parse(sessionStorage.getItem("user"));
    if (this.sid == null) {
      this.$message({
        type: "error",
        message: "请重新登录。",
      });
    }
    let that = this;
    axios
      .post("http://localhost:3000/course", { sid: that.sid })

      .then((res) => {
        // console.log(res.data);
        that.courses = res.data.courses;
        that.stu = res.data.stu;
        if (that.stu != 666) {
          for (let j = 0; j < this.courses.length; j++) {
            this.courses[j].work = 0;
          }
          for (let i = 0; i < this.stu.courses.length; i++) {
            if (this.stu.courses[i][1] == 0)
              for (let j = 0; j < this.courses.length; j++) {
                if (this.courses[j].id == this.stu.courses[i][0]) {
                  this.courses[j].work = 1;
                }
              }
          }
        }
        that.generate();
        that.$forceUpdate();
        // this.$message({
        //   message:"加载完成"
        // })
      })
      .catch((err) => {
        console.log(err);
        that.$message({
          type: "error",
          message: "网络错误，请检查网络。",
        });
      });
  },
};
</script>

<style scoped>
.content-wrapper {
  padding: 20px;
}
</style>