<template>
  <div class="content-wrapper">
    <div class="card-body" style="width: 400px; margin: 100px auto">
      <el-form status-icon label-width="100px" class="demo-ruleForm">
        <el-form-item label="学号">
          <el-input v-model="userid" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码">
          <el-input
            type="password"
            v-model="password"
            autocomplete="off"
          ></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="submit">提交</el-button>
          <el-button @click="reset">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      userid: "",
      password: "",
    };
  },

  methods: {
    
    submit() {
      console.log("login");
      if (this.userid == "" || this.password == "") {
        this.$message({ type: "primary", message: "请输入用户名(学号)或密码" });
        return;
      }
      let that = this;
      axios
        .post("http://localhost:3000/users/login", {
          userid: that.userid,
          password: that.password,
        })
        .then((res) => {
          if (res.data.status === 1) {
            sessionStorage.setItem(
              "user",
              JSON.stringify(parseInt(res.data.id))
            );
            sessionStorage.setItem("login", JSON.stringify(true));
            sessionStorage.setItem("username", JSON.stringify(res.data.uname));
            this.$bus.$emit("username")
            this.$message({ type: "success", message: "登录成功" });
            this.$router.replace({ name: 'DisplayCourses' });
          } else
            this.$message({ type: "error", message: "用户名或密码错误" });
        })
        .catch((err) => {
          console.log(err);
          this.$message({ type: "error", message: "网络错误" });
        });
    },
    reset() {
      this.userid = "";
      this.password = "";
    },
  },
  mounted() {
    
  },
};
</script>

<style scoped>
.content-wrapper {
  padding: 10px;
}

.button {
  float: left;
  margin: 5px;
}
</style>