<template>
  <div class="content-wrapper">
    <div class="card-body">
      <div>
        <span style="float: left; margin-top: 10px; margin-right: 5px"
          >搜索</span
        >
      </div>
      <div style="float: left">
        <el-input v-model="search" placeholder="请输入内容"></el-input>
      </div>
      <el-button
        type="primary"
        size="small"
        icon="el-icon-search"
        @click="searchcourse(1)"
        style="float: left; margin-left: 10px; margin-top: 4px"
        >名称搜索</el-button
      >
      <el-button
        type="primary"
        size="small"
        icon="el-icon-search"
        @click="searchcourse(2)"
        style="float: left; margin-left: 10px; margin-top: 4px"
        >模糊搜索</el-button
      >

      <el-table :data="tableData" height="500" stripe style="width: 100%">
        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          label="id"
          prop="id"
        >
        </el-table-column>

        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          prop="name"
          label="name"
        >
        </el-table-column>

        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          prop="teacher"
          label="teacher"
        >
        </el-table-column>

        <el-table-column align="center" label="time">
          <template slot-scope="scope">
            <span
              :key="index"
              v-show="item"
              v-for="(item, index) in scope.row.week"
              >{{
                "周" +
                week[index] +
                (" : " + scope.row.time[index]).replace(",", "-")
              }}<br
            /></span>
          </template>
        </el-table-column>

        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          prop="place[1]"
          label="place"
        >
        </el-table-column>

        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          prop="enddate"
          label="url"
        >
          <template slot-scope="scope">
            <div style="height: 40px; line-height: 40px">
              <span>
                <el-link
                  :type="typecolor[scope.row.work]"
                  @click="view(scope.row)"
                  >详情</el-link
                >
              </span>

              <!-- <el-input :ref='"enddateinput"+"tag5"+"&"+scope.$index' @blur="switchShow(scope.$index,'tag5')" clearable
                        @keyup.enter.native="$event.target.blur" v-show="scope.row.is_show_tag5" size="mini"
                        v-model="scope.row.tag5"></el-input> -->
            </div>
          </template>
        </el-table-column>

        <el-table-column
          :show-overflow-tooltip="true"
          align="center"
          label="edit"
          width="200"
          v-if="sid == 666"
        >
          <template slot-scope="scope">
            <el-button-group>
              <el-button
                size="small"
                type="warning"
                icon="el-icon-edit"
                @click="edit(scope.row)"
                >修改</el-button
              >
              <el-button
                size="small"
                type="danger"
                icon="el-icon-delete"
                @click="remove(scope.row)"
                >删除</el-button
              >
            </el-button-group>
          </template>
        </el-table-column>
      </el-table>
      <el-button size="small" type="success" class="button" @click="add" v-if="sid == 666"
        >添加</el-button
      >
    </div>
  </div>
</template>
<script>
import axios from "axios";

export default {
  name: "DisplayCourses",
  data() {
    return {
      sid: "",
      tableData: [],
      // [
      //   {
      //     name: "数据结构",
      //     id: 0,
      //     classes: [2020211311, 2020211312],
      //     week: ["一", "二"],
      //     time: [
      //       ["8:30", "9:30"],
      //       ["9:40", "12:00"],
      //     ],
      //     place: "教学楼102",
      //     teacher: "wgy",
      //     info: ["简介信息", "作业信息", "考试信息"],
      //     url: "/course/0",
      //   },
      //   {
      //     name: "计算机组成原理",
      //     id: 1,
      //     classes: [2020211312],
      //     week: ["三"],
      //     time: [["8:30,9:30"]],
      //     place: "教学楼119",
      //     teacher: "wgy",
      //     info: ["简介信息", "作业信息", "考试信息"],
      //     url: "/course/1",
      //   },
      // ],
      week: ["一", "二", "三", "四", "五", "六", "日"],
      search: "",
      stu: "",
      typecolor: ["primary", "danger"],
    };
  },

  methods: {
    // 切换input框的显示状态
    /* switchShow(index, tag, tmp_this = this) {
      // 因为涉及到调用不同名称的变量, 不清楚怎么写, 只能先用switch case解决
      switch (tag) {
        case "tag1":
          tmp_this.tableData[index].is_show_tag1 =
            !tmp_this.tableData[index].is_show_tag1;
          break;
        case "tag2":
          tmp_this.tableData[index].is_show_tag2 =
            !tmp_this.tableData[index].is_show_tag2;
          break;
        case "tag3":
          tmp_this.tableData[index].is_show_tag3 =
            !tmp_this.tableData[index].is_show_tag3;
          break;
        case "tag4":
          tmp_this.tableData[index].is_show_tag4 =
            !tmp_this.tableData[index].is_show_tag4;
          break;
        case "tag5":
          tmp_this.tableData[index].is_show_tag5 =
            !tmp_this.tableData[index].is_show_tag5;
          break;
        // ...
      }
      tmp_this.tableData = [...tmp_this.tableData]; //因为我table绑定的表格数据是后接过来赋值的，所以需要这步操作，如果没有1、2步骤这个可以不加。后面也一样
    }, */

    // 显示input框, 使光标焦点当前input框
    // changeEnddate(index, tag, rowdata) {
    //   this.switchShow(index, tag, this);
    //   // console.log('enddateinput' + index, typeof 'enddateinput' + index);
    //   // console.log(rowdata, typeof rowdata);
    //   setTimeout(() => {
    //     //定时器是为了避免没有获取到dom的情况报错，所以象征性的给1毫秒让他缓冲
    //     this.$refs["enddateinput" + tag + "&" + index].focus();
    //     //el-input的autofocus失效，所以用这个方法。对应在template里的refs绑定值
    //   }, 1);
    // },
    searchcourse(i) {
      let that = this;
      if (this.search == "")
        this.$message({
          type: "error",
          message: "请输入内容",
        });
      axios
        .post("http://localhost:3000/course/search", {
          sid: that.sid,
          mode: i,
          word: that.search,
        })
        .then((res) => {
          console.log(res.data);
          that.tableData = res.data.courses;
        })
        .catch((err) => {
          console.log(err);
          that.$message({
            type: "error",
            message: "网络错误，请检查网络。",
          });
        });
    },
    add: function () {
      this.$router.push({
        name: "NewCourse",
      });
    },
    edit: function (item) {
      this.$router.push({
        name: "EditCourse",
        params: {
          id: item.id,
        },
      });
      console.log(item);
    },
    remove: function (index) {
      console.log(index);
      // this.tableData.splice(index, 1);
    },
    view: function (item) {
      this.$router.push({
        name: "ViewCourse",
        params: {
          id: item.id,
          sid: this.sid,
        },
      });
      console.log(item);
    },
  },
  mounted() {
    this.sid = JSON.parse(sessionStorage.getItem("user"));
    if (this.sid == null) {
      this.$message({
            type: "error",
            message: "请重新登录。",
          });
    }
    let that = this;
    axios
      .post("http://localhost:3000/course", { sid: that.sid })

      .then((res) => {
        console.log(res.data);
        that.tableData = res.data.courses;
        that.stu = res.data.stu;
        if (that.stu != 666) {
          for (let j = 0; j < this.tableData.length; j++) {
            this.tableData[j].work = 0;
          }
          for (let i = 0; i < this.stu.courses.length; i++) {
            if (this.stu.courses[i][1] == 0)
              for (let j = 0; j < this.tableData.length; j++) {
                if (this.tableData[j].id == this.stu.courses[i][0]) {
                  this.tableData[j].work = 1;
                }
              }
          }
        }
      })
      .catch((err) => {
        console.log(err);
        that.$message({
            type: "error",
            message: "网络错误，请检查网络。",
          });
      });
  },
  beforeCreate() {},
};
</script>

<style scoped>
.content-wrapper {
  padding: 10px;
}

.button {
  float: left;
  margin: 5px;
}
</style>