<template>
  <div>
    <el-page-header
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @back="back"
      content="编辑课程"
    >
    </el-page-header>
    <!-- <el-button
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @click="back"
      size="mini"
      >返回</el-button
    > -->
    <el-descriptions
      contentStyle="width:500px"
      style="margin-left: 5px; margin-top: 5px"
      :column="2"
      border
      height="400"
    >
      <el-descriptions-item label="课程名"
        ><el-col :span="8"><el-input v-model="course.name"></el-input></el-col>
      </el-descriptions-item>
      <el-descriptions-item label="id编号">{{
        course.id
      }}</el-descriptions-item>
      <el-descriptions-item label="上课时间">
        <div
          :key="index"
          v-for="(day, index) in course.week"
          style="float: left; margin-left: 10px"
        >
          <el-checkbox size="mini" :label="day" v-model="course.week[index]">{{
            "周" + week[index]
          }}</el-checkbox>
        </div>
        <br />
        <div :key="k" v-for="(day, k) in week">
          <div v-show="course.week[k]">
            周{{ day }}
            <!-- <el-time-picker
              is-range
              size="small"
              v-model="course.time[k]"
              range-separator="至"
              start-placeholder="开始时间"
              end-placeholder="结束时间"
              placeholder="选择时间范围"
              format="HH:mm"
              value-format="HH:mm"
            ></el-time-picker> -->
            <el-select v-model="course.time[k][0]" placeholder="选择开始时间">
              <el-option
                v-for="item in options1"
                :key="item"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
            <el-select v-model="course.time[k][1]" placeholder="选择结束时间">
              <el-option
                v-for="item in options2"
                :key="item"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </div>
        </div>
      </el-descriptions-item>
      <el-descriptions-item :span="2" label="班级">
        <el-col :span="20">
          <el-input
            size="small"
            type="number"
            v-model="course.classes"
            readonly
          ></el-input>
        </el-col>
      </el-descriptions-item>
      <el-descriptions-item label="地点">
        <el-dropdown @command="chooseplace">
          <span class="el-dropdown-link">
            {{ showplace }}<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="19">西土城主楼</el-dropdown-item>
            <el-dropdown-item command="20">西土城教1楼</el-dropdown-item>
            <el-dropdown-item command="21">西土城教2楼xx</el-dropdown-item>
            <el-dropdown-item command="22">西土城教3楼</el-dropdown-item>
            <el-dropdown-item command="23">西土城教4楼</el-dropdown-item>
            <el-dropdown-item command="128">沙河教学楼S1</el-dropdown-item>
            <el-dropdown-item command="129">沙河实验楼S2,S3</el-dropdown-item>
            <el-dropdown-item command="131"
              >沙河教学实验综合楼</el-dropdown-item
            >
          </el-dropdown-menu>
        </el-dropdown>
      </el-descriptions-item>
      <el-descriptions-item label="详细地点"
        ><el-col :span="8"
          ><el-input v-model="course.place[1]"></el-input></el-col
      ></el-descriptions-item>
      <el-descriptions-item label="授课老师"
        ><el-col :span="8"
          ><el-input v-model="course.teacher"></el-input></el-col
      ></el-descriptions-item>
      <el-descriptions-item label="课程信息"
        ><el-input type="textarea" autosize v-model="course.info[0]"></el-input
      ></el-descriptions-item>
      <el-descriptions-item label="考试信息">
        <el-input type="textarea" autosize v-model="course.info[2]"></el-input
      ></el-descriptions-item>
      <el-descriptions-item label="作业信息"
        ><el-input type="textarea" autosize v-model="course.info[1]"></el-input
      ></el-descriptions-item>
      <!-- <el-descriptions-item label="作业信息">
        <div>（简介）</div>
        <el-divider></el-divider>
        <div>提交窗口</div></el-descriptions-item
      > -->
      <el-descriptions-item label="发布作业与资料"
        ><el-upload
          class="upload-demo"
          action="http://localhost:3000/workfile"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          multiple
          :limit="1"
          :on-exceed="handleExceed"
          :file-list="fileList"
          :http-request="upload"
        >
          <el-button size="small" type="primary">点击上传资料</el-button>
          <div slot="tip" class="el-upload__tip">
            只能上传doc/txt/png文件,且不超过500kb
          </div> </el-upload
        ><el-button size="small" type="primary" @click="newwork"
          >发布提醒</el-button
        ></el-descriptions-item
      >
      <el-descriptions-item label="下载学生作业"
        ><el-button size="small" @click="check">检查完成情况</el-button
        ><br /><br />
        <el-col :span="20"
          ><el-input
            size="small"
            type="number"
            v-model="studentid"
            placeholder="输入待下载学生学号"
          ></el-input></el-col
        ><el-button size="small" @click="download"
          >下载</el-button
        ></el-descriptions-item
      >
    </el-descriptions>
    <el-button
      type="success"
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @click="confirm"
      size="mini"
      >确认</el-button
    >
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      course: {
        name: "",
        id: 0,
        classes: "",
        // week: [true,false,false,true,false,false,false],
        week: [false, false, false, false, false, false, false],
        time: [
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
        ],
        place: [0, ""],
        teacher: "",
        info: ["", "", ""],
        url: "/course/0",
      },
      sid: "",
      week: ["一", "二", "三", "四", "五", "六", "日"],
      work: false,
      studentid: "",
      placename: new Array(200),
      void: "",
      options1: [
        "08:00",
        "08:50",
        "09:50",
        "10:40",
        "11:30",
        "13:00",
        "13:50",
        "14:45",
        "15:40",
        "16:35",
        "17:25",
        "18:30",
        "19:20",
        "20:10",
      ],
      options2: [
        "08:45",
        "09:35",
        "10:35",
        "11:25",
        "12:15",
        "13:45",
        "14:35",
        "15:30",
        "16:25",
        "17:20",
        "18:10",
        "19:15",
        "20:05",
        "20:55",
      ],
    };
  },
  computed: {
    showplace: {
      get() {
        this.course.place[0];
        return this.placename[this.course.place[0]] + this.void;
      },
    },
  },
  methods: {
    chooseplace(command) {
      this.course.place[0] = command;
      this.void = " ";
      this.void = "";
      // this.showplace();
    },
    newwork() {
      this.work = true;
    },
    check() {
      let that = this;
      axios
        .post("http://localhost:3000/course/check", {
          sid: that.sid,
          course: that.course,
        })
        .then((res) => {
          // console.log(res.data);
          alert(res.data);
        })
        .catch((err) => {
          console.log(err);
          this.$message({
            type: "error",
            message: "网络问题",
          });
        });
    },
    download(param) {
      let that = this;
      let fileobj = param.file;
      let form = new FormData();
      form.append("file", fileobj);
      // form.append("params", that.course.id);
      console.log(form);
      axios({
        method: "post",
        url:
          "http://localhost:3000/course/download/" +
          that.course.id +
          "/" +
          that.studentid,
        headers: { "content-type": "multipart/form-data" },
      })
        .then((res) => {
          console.log(res.data);

          window.open("http://localhost:3000" + res.data);
        })
        .catch((err) => {
          console.log(err);
          this.$message({ type: "error", message: "下载失败" });
        });
    },
    back() {
      this.$destroy();
      this.$router.replace({ name: "DisplayCourses" });
    },
    confirm() {
      this.$confirm("修改课程, 是否确认?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          let that = this;
          that.course.place[0] = parseInt(that.course.place[0]);
          axios
            .post("http://localhost:3000/course/edit", {
              sid: that.sid,
              course: that.course,
              work: that.work,
            })
            .then((res) => {
              console.log(res.data);
              if (res.data == "0")
                this.$message({
                  type: "error",
                  message: "没有权限!",
                });
              else if (res.data == "1")
                this.$message({
                  type: "success",
                  message: "修改成功!",
                });
            })
            .catch((err) => {
              console.log(err);
              this.$message({
                type: "error",
                message: "网络问题",
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消",
          });
        });

      this.$router.replace({ name: "DisplayCourses" });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${files.length} 个文件，共选择了 ${
          files.length + fileList.length
        } 个文件`
      );
    },
    beforeRemove(file, fileList) {
      console.log(fileList);
      return this.$confirm("确定移除 ${file.name}?");
    },
    upload(param) {
      let that = this;
      let fileobj = param.file;
      let form = new FormData();
      form.append("file", fileobj);
      // form.append("params", that.course.id);
      console.log(form);
      axios({
        method: "post",
        url: "http://localhost:3000/course/workfile/" + that.course.id,
        headers: {
          "Content-Type": "multipart/form-data",
        },
        data: form /* { file: form, id: that.course.id } */,
        params: that.course.id,
      })
        .then((res) => {
          alert(res.data);
        })
        .catch((err) => {
          console.log(err);
          this.$message({ type: "error", message: "上传失败" });
        });
    },
  },
  mounted() {
    console.log(this.$route.params);
    this.placename[19] = "西土城主楼";
    this.placename[20] = "西土城教1楼";
    this.placename[21] = "西土城教2楼";
    this.placename[22] = "西土城教3楼";
    this.placename[23] = "西土城教4楼";
    this.placename[128] = "沙河教学楼";
    this.placename[129] = "沙河实验楼";
    this.placename[131] = "沙河教学实验楼";

    // this.sid = JSON.parse(sessionStorage.getItem("user"));
    let that = this;
    this.sid = JSON.parse(sessionStorage.getItem("user"));
    console.log(this.$route.params);
    axios
      .post("http://localhost:3000/course/view", {
        cid: that.$route.params.id,
        sid: sessionStorage.getItem("user"),
      })
      .then((res) => {
        that.course = res.data;
        that.course.place[0] = "" + that.course.place[0];
      })
      .catch((err) => {
        console.log(err);
        that.$message({
          type: "error",
          message: "网络错误，请检查网络。",
        });
      });
  },
  beforeCreate() {},
};
</script>



<style scoped>
</style>