<template>
  <div class="block">
    <!-- <h2>闹钟设置</h2> -->
    <div>
      <span style="margin-top: 5px; font-size: 14px">已有闹钟：</span>
      <div :key="index" v-for="(i, index) in timer">
        <span style="margin-right: 5px">{{ i.name }}</span>
        <el-date-picker
          v-model="i.time"
          type="datetime"
          readonly
          :editable="false"
        ></el-date-picker>
        <el-button plain @click="Cancel(i)">取消</el-button>
      </div>
    </div>
    <el-input
      placeholder="请输入闹钟名称"
      prefix-icon="el-icon-edit-outline"
      v-model="event"
      style="width: 220px; margin-top: 80px"
      clearable
    >
    </el-input>
    <br /><br />
    <el-date-picker
      :editable="false"
      v-model="setTime"
      type="datetime"
      placeholder="选择闹钟日期时间"
      :picker-options="pickerOption"
      >>
    </el-date-picker>
    <br /><br />
    <el-select
      v-model="value"
      placeholder="请选择闹钟周期"
      style="width: 220px"
    >
      <el-option
        v-for="item in options"
        :key="item.value"
        :label="item.label"
        :value="item.value"
      >
      </el-option>
    </el-select>
    <br /><br />
    <el-row>
      <el-button plain @click="Mode(number)">开始闹钟</el-button>
    </el-row>
    <br />
    <!-- <el-row>
      <el-button plain @click="Cancel()">取消闹钟</el-button>
    </el-row> -->
  </div>
</template>

  <script>
import audio from "../assets/Alarm02.wav";
export default {
  name: "Clock",
  data() {
    return {
      sid: 2020211523,
      //控制时间范围
      pickerOption: {
        disabledDate(time) {
          return time.getTime() < Date.now() - 8.64e7;
        },
      },

      options: [
        {
          value: "1",
          label: "仅一次",
        },
        {
          value: "2",
          label: "每天一次",
        },
        {
          value: "3",
          label: "每周一次",
        },
      ],
      gtime: "",
      setTime: "",
      value: "",
      event: "",
      number: 0,
      timer: [],
      void: "",
    };
  },

  methods: {
    gettime(num) {
      // console.log(this.showtime);
      this.gtime = num;
    },
    alarm() {
      let a = new Audio();
      a.src = audio;
      a.play();
    },
    Mode(id) {
      // var test = new Date(set_time);

      this.number += 1;
      let set_time = this.setTime;
      let MyEvent = this.event;
      let that = this;
      if (this.value == "" || this.event == "" || this.setTime == "") {
        this.$message({
          type: "error",
          message: "请补全信息!",
        });
        return;
      }

      if (this.value == 1) {
        //仅一次
        this.$message({
          type: "success",
          message: "闹钟 " + MyEvent + " 一次提醒已开始!",
        });
        this.timer.push({
          id: id,
          name: this.event,
          time: this.setTime,
          tick: setInterval(function () {
            // var myDate = new Date();//////////////////////////////////////////
            var myDate = new Date(that.gtime);
            console.log(myDate);
            // var time = formatDate(myDate);
            // console.log("tick");
            if (myDate > set_time) {
              that.$message({
                type: "success",
                message: "闹钟 " + MyEvent + " 时间到了!",
              });
              that.alarm();
              clearInterval(that.timer[id].tick);
              // for (let t = id; t < this.timer.length; t++) {
              //   this.timer[t] = this.timer[t + 1];
              //   this.timer[t].id--;
              // }
              // this.timer.pop();
              // that.timer = that.timer.filter((res) => res.tick != "");
            }
          }, 500),
        });
      } //formatDate函数转换为时间特定格式 eg:2022-4-10 23:04:50
      else if (this.value == 2) {
        //每天一次
        // var set_time_number = new Date(this.setTime);
        var flag = false;
        this.$message({
          type: "success",
          message: "闹钟 " + MyEvent + " 每天一次提醒已开始!",
        });

        this.timer.push({
          id: id,
          name: this.event,
          time: this.setTime,
          tick: setInterval(function () {
            // var myDate2 = new Date();////////////////////////////////////////////
            var myDate2 = new Date(that.gtime);
            if (myDate2 > that.timer[id].time) {
              flag = true;
              that.$message({
                type: "success",
                message: "闹钟 " + MyEvent + " 时间到了!",
              });
              that.alarm();
            }
            if (flag) {
              that.timer[id].time.setTime(
                that.timer[id].time.getTime() + 1000 * 60 * 60 * 24
              ); //将设定时间调为下一天
              that.timer[id].time = that.timer[id].time.toLocaleString();
              that.timer[id].time = new Date(that.timer[id].time);
              flag = false;
            }
          }, 500),
        });
      } else {
        //每周一次
        // var set_time_number2 = new Date(this.setTime);
        // var test3 = set_time_number2.getTime();
        var flag2 = false;
        this.$message({
          type: "success",
          message: "闹钟 " + MyEvent + " 每周一次提醒已开始!",
        });

        this.timer.push({
          id: id,
          name: this.event,
          time: this.setTime,
          tick: setInterval(() => {
            // var myDate3 = new Date();///////////////////////////////////////
            var myDate3 = new Date(that.gtime);
            // var time3 = formatDate(myDate3); //现在时间的字符串

            if (myDate3 > that.timer[id].time) {
              flag2 = true;
              that.$message({
                type: "success",
                message: "闹钟 " + MyEvent + " 时间到了!",
              });
              that.alarm();
            }

            if (flag2) {
              that.timer[id].time.setTime(
                that.timer[id].time.getTime() + 1000 * 60 * 60 * 24 * 7
              ); //将设定时间调为下一天
              that.timer[id].time = that.timer[id].time.toLocaleString();
              that.timer[id].time = new Date(that.timer[id].time);
              // demo3 = formatDate(set_time_number); //将设定时间转换为字符串
              flag2 = false;
            }
          }, 500),
        });
      }
      console.log(this.timer);
      this.$forceUpdate();
      //  this.exsit = false;
    },

    Cancel(i) {
      // this.$forceUpdate();
      this.$confirm("此操作将永久删除" + i.name + "闹钟，是否继续？", "提示", {
        iconClass: "el-icon-question",
        confirmButtonText: "确认",
        cancelButtonText: "取消",
        showClose: true,
        type: "warning",
      })
        .then(() => {
          if (i.tick != "") {
            clearInterval(i.tick);
            console.log(i);
            i.tick = "";
            // i = undefined;
            for (let t = i.id; t < this.timer.length - 1; t++) {
              this.timer[t] = this.timer[t + 1];
              this.timer[t].id--;
            }
            this.timer.pop();
            this.$message({
              type: "success",
              message: "删除成功！",
            });
            this.number=this.timer.length;
          } else {
            this.$message({
              type: "warning",
              message: "你还没有创建闹钟，无法删除~",
            });
          }
        })
        .catch(() => {
          //删除
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
  },
  mounted() {
    this.sid = JSON.parse(sessionStorage.getItem("user"));
    if (this.sid == null)
      this.$message({
        type: "error",
        message: "请重新登录。",
      });
    let that = this;
    setInterval(() => {
      // console.log(that)
      that.$bus.$on("time", this.gettime);
      
      // that.$forceUpdate();
    }, 500);
    // console.log(this.$bus.$emit())
  },
};

/* export const formatDate = function (date) {
  //时间格式转换函数（Date -> String）
  var year = date.getFullYear();
  var month = date.getMonth() + 1;
  var day = date.getDate();
  var h = date.getHours();
  var m = date.getMinutes();
  var s = date.getSeconds();
  var time = year + "-";
  if (month < 10) {
    time += "0";
  }
  time += month + "-";
  if (day < 10) {
    time += "0";
  }
  time += day + " ";
  if (h < 10) {
    time += "0";
  }
  time += h + ":";
  if (m < 10) {
    time += "0";
  }
  time += m + ":";
  if (s < 10) {
    time += "0";
  }
  time += s;
  return time;
  // return year + '-' + month + '-' + day + ' ' + h + ':' + m + ':' + s
}; */
</script>