<template>
  <div>
    <!-- <el-button
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @click="back"
      size="mini"
      >返回</el-button
    > -->
    <el-page-header
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @back="back"
      content="查看课程"
    >
    </el-page-header>
    <el-descriptions
      contentStyle="width:500px"
      style="margin-left: 5px; margin-top: 5px"
      :column="2"
      border
      height="400"
    >
      <el-descriptions-item label="课程名"
        >{{ course.name }}
      </el-descriptions-item>
      <el-descriptions-item label="id编号">{{
        course.id
      }}</el-descriptions-item>
      <el-descriptions-item label="上课时间">
        <div v-for="(day, index) in week" :key="index">
          <div v-show="course.week[index]">
            周{{ day }}
            <el-time-picker
              readonly
              is-range
              size="small"
              v-model="course.time[index]"
              range-separator="至"
              start-placeholder="开始时间"
              end-placeholder="结束时间"
              placeholder="选择时间范围"
              format="HH:mm"
              value-format="HH:mm"
            ></el-time-picker>
          </div>
        </div>
      </el-descriptions-item>
      <el-descriptions-item :span="2" label="班级">
        <el-col :span="8">
          <el-input
            size="small"
            type="number"
            v-model="course.classes"
            readonly
          ></el-input>
        </el-col>
      </el-descriptions-item>
      <el-descriptions-item label="详细地点">{{
        course.place[1]
      }}</el-descriptions-item>
      <el-descriptions-item label="授课老师">{{
        course.teacher
      }}</el-descriptions-item>
      <el-descriptions-item label="课程信息"
        ><el-input
          readonly
          type="textarea"
          autosize
          v-model="course.info[0]"
        ></el-input
      ></el-descriptions-item>
      <el-descriptions-item label="考试信息"
        ><el-input
          readonly
          type="textarea"
          autosize
          v-model="course.info[2]"
        ></el-input>
      </el-descriptions-item>
      <el-descriptions-item label="作业信息"
        ><el-input
          readonly
          type="textarea"
          autosize
          v-model="course.info[1]"
        ></el-input>
        <el-button size="small" @click="download">下载资料</el-button>
      </el-descriptions-item>
      <el-descriptions-item label="作业提交"
        ><el-upload
          class="upload-demo"
          action="http://localhost:3000/file"
          :on-preview="handlePreview"
          :on-remove="handleRemove"
          :before-remove="beforeRemove"
          multiple
          :limit="1"
          :on-exceed="handleExceed"
          :file-list="fileList"
          :http-request="upload"
        >
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">
            只能上传doc/txt/png文件,且不超过500kb
          </div>
        </el-upload></el-descriptions-item
      >
      <!-- <el-descriptions-item label="作业信息">
        <div>（简介）</div>
        <el-divider></el-divider>
        <div>提交窗口</div></el-descriptions-item
      > -->
    </el-descriptions>
    <el-button
      type="success"
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @click="confirm"
      size="mini"
      >确认</el-button
    >
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      course: {
        name: "",
        id: 0,
        classes: "",
        // week: [true,false,false,true,false,false,false],
        week: [false, false, false, false, false, false, false],
        time: [
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
        ],
        place: [-1, ""],
        teacher: "",
        info: ["", "", ""],
        url: "/course/0",
      },
      week: ["一", "二", "三", "四", "五", "六", "日"],
      fileList: [],
      //   fileList: [{name:"food.jpg",url:""}]
    };
  },
  methods: {
    back() {
      // this.$destroy();
      this.$router.replace({ name: "DisplayCourses" });
    },
    confirm() {
      // this.$destroy();
      this.$router.replace({ name: "DisplayCourses" });
    },
    handleRemove(file, fileList) {
      console.log(file, fileList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 3 个文件，本次选择了 ${files.length} 个文件，共选择了 ${
          files.length + fileList.length
        } 个文件`
      );
    },
    beforeRemove(file, fileList) {
      console.log(fileList);
      return this.$confirm("确定移除 ${file.name}?");
    },
    upload(param) {
      let that = this;
      let fileobj = param.file;
      let form = new FormData();
      form.append("file", fileobj);
      // form.append("params", that.course.id);
      console.log(form);
      axios({
        method: "post",
        url:
          "http://localhost:3000/course/file/" +
          that.course.id +
          "/" +
          sessionStorage.getItem("user"),
        headers: {
          "content-type": "multipart/form-data",
        },
        // params: that.course.id,
        data:form,
      })
        .then((res) => {
          alert(res.data);
        })
        .catch((err) => {
          console.log(err);
          this.$message({ type: "error", message: "上传失败" });
        });
    },
    download(param) {
      let that = this;
      let fileobj = param.file;
      let form = new FormData();
      form.append("file", fileobj);
      // form.append("params", that.course.id);
      console.log(form);
      axios({
        method: "get",
        url: "http://localhost:3000/course/workfile/" + that.course.id + '/'+ that.$route.params.sid,
        headers: { "content-type": "multipart/form-data" },
      })
        .then((res) => {
          console.log(res.data);
          if (res.data == 0) {
            this.$message({ type: "error", message: "暂无资料" });
            return;
          }
          console.log(res.data)
          window.open(("http://localhost:3000" + res.data));
        })
        .catch((err) => {
          console.log(err);
          this.$message({ type: "error", message: "下载失败" });
        });
    },
  },
  mounted() {
    console.log(this.$route.params);

    let that = this;
    console.log(this.$route.params);
    axios
      .post("http://localhost:3000/course/view", {
        cid: that.$route.params.id,
        sid: sessionStorage.getItem("user"),
      })
      .then((res) => {
        that.course = res.data;
      })
      .catch((err) => {
        console.log(err);
        that.$message({
            type: "error",
            message: "网络错误，请检查网络。",
          });
      });
  },
  beforeCreate() {
    
  },
};
</script>








