  mounted() {
    this.initCanvas();
    let that = this;
    this.t = setInterval(() => {
      that.$bus.$on("maptime", that.gettime);

      this.value1 = this.tableData[this.value2].place[0];
    }, 500);
    this.sid = JSON.parse(sessionStorage.getItem("user"));

    axios
      .post("http://localhost:3000/course", { sid: that.sid })

      .then((res) => {
        // console.log(res.data);
        let courses = res.data.courses;
        let k = 0;
        // that.stu = res.data.stu;
        for (let i = 0; i < courses.length; i++) {
          for (let j = 0; j < 7; j++)
            if (courses[i].week[j] == true) {
              that.tableData.push({
                id: k++,
                name: courses[i].name,
                place: courses[i].place,
                time:
                  "周" +
                  (j + 1) +
                  " " +
                  courses[i].time[j][0] +
                  "-" +
                  courses[i].time[j][1],
              });
            }
        }
      })
      .catch((err) => {
        console.log(err);
        that.$message({
          type: "error",
          message: "网络错误，请检查网络。",
        });
      });
  },
  beforeDestroy() {
    clearInterval(this.t);
  },
  methods: {
    sel() {
      this.value1 = this.tableData[this.value2].place[0];
    },
    gettime(num) {
      this.time = num;
    },
    initCanvas() {
      let canvas = this.$refs.myCanvas;
      let ctx = canvas.getContext("2d");
      let img = new Image();
      let that = this;
      canvas.width = 1990;
      canvas.height = 1200;
      img.src = this.src;
      img.onload = () => {
        let iw = img.width;
        let ih = img.height;
        let local = that.calculate(iw, ih, canvas.width, canvas.height);
        ctx.fill();
        ctx.drawImage(img, 0, 0, local.pw, local.ph);
      };
    },
    back() {
      this.visible = false;
      this.$forceUpdate();
      this.$message({
        type: "info",
        message: "已取消",
      });
    },

    draw(x1, y1, x2, y2, module) {
      //画线函数
      var c = this.$refs.myCanvas;
      var ctx = c.getContext("2d");
      ctx.beginPath();
      ctx.lineWidth = 5;
      if (module == 0) {
        //第一种路径算法划线
        ctx.strokeStyle = "blue";
        ctx.moveTo(x1 * 2, y1 * 2);
        ctx.lineTo(x2 * 2, y2 * 2);
        ctx.stroke();
        ctx.save();
      } else if (module == 1) {
        //第二种路径算法划线
        ctx.strokeStyle = "red";
        ctx.moveTo(x1 * 2, y1 * 2);
        ctx.lineTo(x2 * 2, y2 * 2);
        ctx.stroke();
        ctx.save();
      } else {
        //第三种路径算法划线
        ctx.strokeStyle = "green";
        ctx.moveTo(x1 * 2, y1 * 2);
        ctx.lineTo(x2 * 2, y2 * 2);
        ctx.stroke();
        ctx.save();
      }
    },

    sameCampus(data, i, j, size, mode) {
      let a = Array(size).fill(10000); //[10000, 10000, 10000];
      let b = Array(size).fill(0); //[0, 0, 0];
      let c = Array(size).fill(-1); //[-1, -1, -1];
      let t = 1000;
      if (data[i].school == data[j].school) {
        let re1 = this.dij(data, i, j, size, mode, a, b, c) * t;
        let re2 = 0;
        return { re1, re2, c };
      } else {
        if (data[i].school == 0) {
          //在西土城
          let re1 = this.dij(data, i, 16, size, mode, a, b, c) * t; //或者改成多个门取最小值？
          let re2 = this.dij(data, 127, j, size, mode, a, b, c) * t;
          // re = re1 + re2;
          c[127] = 16;
          return { re1, re2, c };
        } else {
          let re1 = this.dij(data, i, 127, size, mode, a, b, c) * t;
          let re2 = this.dij(data, 16, j, size, mode, a, b, c) * t;
          c[16] = 127;
          // re = re1 + re2;
          return { re1, re2, c };
        }
      }
    },

    change() {
      // this.initCanvas();
      if (this.value1 === "" || this.value === "") return;
      let that = this;
      console.log(this.value, this.value1);
      axios
        .post("http://localhost:3000/users/map", {
          sid: that.sid,
          source: { id: that.value, name: that.buildings[that.value].name },
          dest: { id: that.value1, name: that.buildings[that.value1].name },
        })
        .then()
        .catch();

      let { re1, re2, c } = this.sameCampus(
        this.buildings,
        this.value,
        this.value1,
        this.buildings.length,
        this.radio1
      );
      if (
        this.buildings[this.value].school == this.buildings[this.value1].school
      ) {
        // console.log(this.totaltime);
        let tt = this.time;
        tt.setTime(tt.getTime() + re1 + re2);
        this.$message("预计到达: " + tt.toLocaleString().slice(0, -3));
      } else if (this.buildings[this.value].school == 0) {
        this.bus = [
          [
            new Date(this.time.toLocaleDateString() + " 06:50"),
            new Date(this.time.toLocaleDateString() + " 08:30"),
            new Date(this.time.toLocaleDateString() + " 12:00"),
            new Date(this.time.toLocaleDateString() + " 13:30"),
            new Date(this.time.toLocaleDateString() + " 14:20"),
            new Date(this.time.toLocaleDateString() + " 16:50"),
          ],
          [
            new Date(this.time.toLocaleDateString() + " 09:50"),
            new Date(this.time.toLocaleDateString() + " 11:40"),
            new Date(this.time.toLocaleDateString() + " 13:00"),
            new Date(this.time.toLocaleDateString() + " 15:45"),
            new Date(this.time.toLocaleDateString() + " 17:10"),
            new Date(this.time.toLocaleDateString() + " 18:25"),
            new Date(this.time.toLocaleDateString() + " 21:10"),
          ],
        ];
        let tt = this.time;
        let i;
        tt.setTime(tt.getTime() + re1);
        for (i = 0; i < this.bus[0].length; i++) {
          if (tt < this.bus[0][i]) break;
        }
        if (i != this.bus[0].length) {
          if (
            this.bus[0][i].getTime() + 40 * 60 * 1000 + re2 <
            tt.getTime() + 90 * 60 * 1000 + re2
          ) {
            this.$message({
              duration: 0,
              showClose: true,
              type: "success",
              message:
                "乘" +
                this.bus[0][i].toLocaleTimeString().slice(0, -3) +
                "校车,预计到达: " +
                new Date(
                  this.bus[0][i].getTime() + 40 * 60 * 1000 + re2
                ).toLocaleString(),
            });
          } else {
            this.$message({
              duration: 0,
              showClose: true,
              type: "success",
              message:
                "乘公共汽车,预计到达: " +
                new Date(tt.getTime() + 90 * 60 * 1000 + re2).toLocaleString(),
            });
          }
        } else {
          this.$message({
            duration: 0,
            showClose: true,
            type: "success",
            message:
              "乘公共汽车,预计到达: " +
              new Date(tt.getTime() + 90 * 60 * 1000 + re2).toLocaleString(),
          });
        }
      } else {
        this.bus = [
          [
            new Date(this.time.toLocaleDateString() + " 06:50"),
            new Date(this.time.toLocaleDateString() + " 08:30"),
            new Date(this.time.toLocaleDateString() + " 12:00"),
            new Date(this.time.toLocaleDateString() + " 13:30"),
            new Date(this.time.toLocaleDateString() + " 14:20"),
            new Date(this.time.toLocaleDateString() + " 16:50"),
          ],
          [
            new Date(this.time.toLocaleDateString() + " 09:50"),
            new Date(this.time.toLocaleDateString() + " 11:40"),
            new Date(this.time.toLocaleDateString() + " 13:00"),
            new Date(this.time.toLocaleDateString() + " 15:45"),
            new Date(this.time.toLocaleDateString() + " 17:10"),
            new Date(this.time.toLocaleDateString() + " 18:25"),
            new Date(this.time.toLocaleDateString() + " 21:10"),
          ],
        ];
        let tt = this.time;
        let i;
        tt.setTime(tt.getTime() + re1);
        for (i = 0; i < this.bus[1].length; i++) {
          if (tt < this.bus[1][i]) break;
        }
        if (i != this.bus[1].length) {
          if (
            this.bus[1][i].getTime() + 40 * 60 * 1000 + re2 <
            tt.getTime() + 90 * 60 * 1000 + re2
          ) {
            this.$message({
              duration: 0,
              showClose: true,
              type: "success",
              message:
                "乘" +
                this.bus[1][i].toLocaleTimeString().slice(0, -3) +
                "校车,预计到达: " +
                new Date(
                  this.bus[1][i].getTime() + 40 * 60 * 1000 + re2
                ).toLocaleString(),
            });
          } else {
            this.$message({
              duration: 0,
              showClose: true,
              type: "success",
              message:
                "乘公共汽车,预计到达: " +
                new Date(tt.getTime() + 90 * 60 * 1000 + re2).toLocaleString(),
            });
          }
        } else
          this.$message({
            duration: 0,
            showClose: true,
            type: "success",
            message:
              "乘公共汽车,预计到达: " +
              new Date(tt.getTime() + 90 * 60 * 1000 + re2).toLocaleString(),
          });
      }
      let mode;
      let p;
      for (p = this.value1; c[p] != -1; p = c[p]) {
        mode = 0;
        console.log(this.buildings[p].point);
        if ((p == 16 && c[p] == 127) || (p == 127 && c[p] == 16)) continue;
        if (
          this.radio1 == 2 &&
          this.buildings[p].bicycle == 1 &&
          this.buildings[c[p]].bicycle == 1
        ) {
          mode = 2;
        }
        if (
          (this.buildings[p].bicycle != 1 &&
            this.buildings[c[p]].bicycle != 1) ||
          (this.radio1 != 2 &&
            this.buildings[p].congestion + this.buildings[c[p]].congestion <
              1.5)
        ) {
          mode = 1;
        }
        this.draw(
          this.buildings[p].point[0],
          this.buildings[p].point[1],
          this.buildings[c[p]].point[0],
          this.buildings[c[p]].point[1],
          mode
        );
      }
      // this.draw(
      //   this.buildings[p].point[0],
      //   this.buildings[p].point[1],
      //   this.buildings[c[p]].point[0],
      //   this.buildings[c[p]].point[1],
      //   mode
      // );
    },
    clean() {
      this.initCanvas();
      this.visible = false;
    },

    dij(data, i, j, size, mode, a, b, c) {
      //data-存的数组 size-点的数目，已固定 mode-出行模式
      if (mode == 0) {

        let temp;
        a[i] = 0;
        b[i] = 1;

        for (let s = 0; s < size; s++) {
          //下面是一步Dijkstra的具体实现，在外面再套一层循环就行
          for (let k = 0; k < size; k++) {
            if (
              (b[data[k].next[0]] == 1 ||
                b[data[k].next[1]] == 1 ||
                b[data[k].next[2]] == 1 ||
                b[data[k].next[3]] == 1) &&
              b[k] != 1
            ) {
              //如果点k与连接好的那一片相邻
              b[k] = 2;
              temp = a[k];
              if (b[data[k].next[0]] == 1) {
                //north

                a[k] = Math.min(
                  Math.abs(data[data[k].next[0]].point[1] - data[k].point[1]) +
                    a[data[k].next[0]],
                  a[k]
                );

                if (temp > a[k]) {
                  c[k] = data[k].next[0];
                  temp = a[k];
                }
              }

              if (b[data[k].next[1]] == 1) {
                a[k] = Math.min(
                  Math.abs(data[k].point[1] - data[data[k].next[1]].point[1]) +
                    a[data[k].next[1]],
                  a[k]
                );

                if (temp > a[k]) {
                  c[k] = data[k].next[1];
                  temp = a[k];
                }
              }

              if (b[data[k].next[2]] == 1) {
                a[k] = Math.min(
                  Math.abs(data[k].point[0] - data[data[k].next[2]].point[0]) +
                    a[data[k].next[2]],
                  a[k]
                );

                if (temp > a[k]) {
                  c[k] = data[k].next[2];
                  temp = a[k];
                }
              }

              if (b[data[k].next[3]] == 1) {
                a[k] = Math.min(
                  Math.abs(data[data[k].next[3]].point[0] - data[k].point[0]) +
                    a[data[k].next[3]],
                  a[k]
                );

                if (temp > a[k]) {
                  c[k] = data[k].next[3];
                  temp = a[k];
                }
              }

              //把点k的最短距离找到了，也找到了到达它的最短路径
            }
          }
          let sign = j; //用sign来指示本次把哪个连接上，用j是因为j装好了就够了
          for (let k = 0; k < size; k++) {
            if (b[k] == 2) {
              if (b[sign] == 0) {
                sign = k; //初始化
                continue;
              }
              if (a[sign] > a[k]) {
                sign = k;
              }
            }
          }
          b[sign] = 1;
          for (let k = 0; k < size; k++) {
            if (b[k] == 2) {
              b[k] = 0;
            }
          } //本轮Dijkstra的输者恢复回去。a和c没有恢复，是因为没必要：a总要找一个最短路径，c由a决定
          if (b[j] == 1) {
            break;
          } //目标点到达，可以跳出了
        } //至此，j的数据排列好了。包括它的距离（用a数组表示）、每一步怎么走（一步步看c数组）

        //可以输出了，a数组用来提供距离数据，c数组用来连线

        return a[j];
      }
      if (mode == 1) {
        //时间最短
        //  a = Array(size).fill(10000)//[10000, 10000, 10000];
        // var b = Array(size).fill(0)//[0, 0, 0];
        // var c = Array(size).fill(-1);//[-1, -1, -1];
        let temp;
        let cong;
        a[i] = 0;
        b[i] = 1;

        for (let s = 0; s < size; s++) {
          //下面是一步Dijkstra的具体实现，在外面再套一层循环就行
          for (let k = 0; k < size; k++) {
            if (
              (b[data[k].next[0]] == 1 ||
                b[data[k].next[1]] == 1 ||
                b[data[k].next[2]] == 1 ||
                b[data[k].next[3]] == 1) &&
              b[k] != 1
            ) {
              //如果点k与连接好的那一片相邻
              b[k] = 2;
              temp = a[k];
              if (b[data[k].next[0]] == 1) {
                //north

                cong =
                  (data[data[k].next[0]].congestion + data[k].congestion) / 2;

                a[k] = Math.min(
                  Math.abs(data[data[k].next[0]].point[1] - data[k].point[1]) /
                    cong +
                    a[data[k].next[0]],
                  a[k]
                );

                if (temp > a[k]) {
                  c[k] = data[k].next[0];
                  temp = a[k];
                }
              }

              if (b[data[k].next[1]] == 1) {
                cong =
                  (data[data[k].next[1]].congestion + data[k].congestion) / 2;
                a[k] = Math.min(
                  Math.abs(data[k].point[1] - data[data[k].next[1]].point[1]) /
                    cong +
                    a[data[k].next[1]],
                  a[k]
                );

                if (temp > a[k]) {
                  c[k] = data[k].next[1];
                  temp = a[k];
                }
              }

              if (b[data[k].next[2]] == 1) {
                cong =
                  (data[data[k].next[2]].congestion + data[k].congestion) / 2;

                a[k] = Math.min(
                  Math.abs(data[k].point[0] - data[data[k].next[2]].point[0]) /
                    cong +
                    a[data[k].next[2]],
                  a[k]
                );

                if (temp > a[k]) {
                  c[k] = data[k].next[2];
                  temp = a[k];
                }
              }

              if (b[data[k].next[3]] == 1) {
                cong =
                  (data[data[k].next[3]].congestion + data[k].congestion) / 2;
                a[k] = Math.min(
                  Math.abs(data[data[k].next[3]].point[0] - data[k].point[0]) /
                    cong +
                    a[data[k].next[3]],
                  a[k]
                );

                if (temp > a[k]) {
                  c[k] = data[k].next[3];
                  temp = a[k];
                }
              }

              //把点k的最短距离找到了，也找到了到达它的最短路径
            }
          }
          let sign = j; //用sign来指示本次把哪个连接上，用j是因为j装好了就够了
          for (let k = 0; k < size; k++) {
            if (b[k] == 2) {
              if (b[sign] == 0) {
                sign = k; //初始化
                continue;
              }
              if (a[sign] > a[k]) {
                sign = k;
              }
            }
          }
          b[sign] = 1;
          for (let k = 0; k < size; k++) {
            if (b[k] == 2) {
              b[k] = 0;
            }
          } //本轮Dijkstra的输者恢复回去。a和c没有恢复，是因为没必要：a总要找一个最短路径，c由a决定
          if (b[j] == 1) {
            break;
          } //目标点到达，可以跳出了
        } //至此，j的数据排列好了。包括它的距离（用a数组表示）、每一步怎么走（一步步看c数组）

        //可以输出了，a数组用来提供距离数据，c数组用来连线
        return a[j];
      }
      if (mode == 2) {
        //车时间最短
        // var a = Array(size).fill(10000)//[10000, 10000, 10000];
        // var b = Array(size).fill(0)//[0, 0, 0];
        // var c = Array(size).fill(-1);//[-1, -1, -1];
        let temp;
        let cong;
        let bike;
        a[i] = 0;
        b[i] = 1;

        for (let s = 0; s < size; s++) {
          //下面是一步Dijkstra的具体实现，在外面再套一层循环就行
          for (let k = 0; k < size; k++) {
            if (
              (b[data[k].next[0]] == 1 ||
                b[data[k].next[1]] == 1 ||
                b[data[k].next[2]] == 1 ||
                b[data[k].next[3]] == 1) &&
              b[k] != 1
            ) {
              //如果点k与连接好的那一片相邻
              b[k] = 2;
              temp = a[k];
              if (b[data[k].next[0]] == 1) {
                //north

                cong =
                  (data[data[k].next[0]].congestion + data[k].congestion) / 2;
                bike =
                  (data[data[k].next[0]].bicycle & data[k].bicycle) * -0.6 + 1;

                a[k] = Math.min(
                  (Math.abs(data[data[k].next[0]].point[1] - data[k].point[1]) *
                    bike) /
                    cong +
                    a[data[k].next[0]],
                  a[k]
                );

                if (temp > a[k]) {
                  c[k] = data[k].next[0];
                  temp = a[k];
                }
              }

              if (b[data[k].next[1]] == 1) {
                cong =
                  (data[data[k].next[1]].congestion + data[k].congestion) / 2;
                bike =
                  (data[data[k].next[1]].bicycle & data[k].bicycle) * -0.6 + 1;

                a[k] = Math.min(
                  (Math.abs(data[k].point[1] - data[data[k].next[1]].point[1]) *
                    bike) /
                    cong +
                    a[data[k].next[1]],
                  a[k]
                );

                if (temp > a[k]) {
                  c[k] = data[k].next[1];
                  temp = a[k];
                }
              }

              if (b[data[k].next[2]] == 1) {
                cong =
                  (data[data[k].next[2]].congestion + data[k].congestion) / 2;
                bike =
                  (data[data[k].next[2]].bicycle & data[k].bicycle) * -0.6 + 1;

                a[k] = Math.min(
                  (Math.abs(data[k].point[0] - data[data[k].next[2]].point[0]) *
                    bike) /
                    cong +
                    a[data[k].next[2]],
                  a[k]
                );

                if (temp > a[k]) {
                  c[k] = data[k].next[2];
                  temp = a[k];
                }
              }

              if (b[data[k].next[3]] == 1) {
                cong =
                  (data[data[k].next[3]].congestion + data[k].congestion) / 2;
                bike =
                  (data[data[k].next[3]].bicycle & data[k].bicycle) * -0.6 + 1;

                a[k] = Math.min(
                  (Math.abs(data[data[k].next[3]].point[0] - data[k].point[0]) *
                    bike) /
                    cong +
                    a[data[k].next[3]],
                  a[k]
                );

                if (temp > a[k]) {
                  c[k] = data[k].next[3];
                  temp = a[k];
                }
              }

              //把点k的最短距离找到了，也找到了到达它的最短路径
            }
          }
          let sign = j; //用sign来指示本次把哪个连接上，用j是因为j装好了就够了
          for (let k = 0; k < size; k++) {
            if (b[k] == 2) {
              if (b[sign] == 0) {
                sign = k; //初始化
                continue;
              }
              if (a[sign] > a[k]) {
                sign = k;
              }
            }
          }
          b[sign] = 1;
          for (let k = 0; k < size; k++) {
            if (b[k] == 2) {
              b[k] = 0;
            }
          } //本轮Dijkstra的输者恢复回去。a和c没有恢复，是因为没必要：a总要找一个最短路径，c由a决定
          if (b[j] == 1) {
            break;
          } //目标点到达，可以跳出了
        } //至此，j的数据排列好了。包括它的距离（用a数组表示）、每一步怎么走（一步步看c数组）

        //可以输出了，a数组用来提供距离数据，c数组用来连线
        return a[j];
      }
    },
    // onSubmit() {
    //   console.log("submit!");
    // },
    recording(e) {
      let x = e.pageX - 209;
      let y = e.pageY - 133;
      this.$message(x + " " + y);
    },
    calculate(pw, ph, w, h) {
      var px = 0;
      var py = 0;
      var uu;
      if (pw < w && ph < h) {
        px = 0.5 * w - 0.5 * pw;
        py = 0.5 * h - 0.5 * ph;
      } else if (ph / pw > h / w) {
        uu = ph;
        ph = h;
        pw = (pw * h) / uu;
        px = 0.5 * w - 0.5 * pw;
      } else {
        uu = pw;
        pw = w;
        ph = (ph * pw) / uu;
        py = 0.5 * h - 0.5 * ph;
      }
      return { px, py, pw, ph };
    },

    // 当用户输入内容开始远程搜索模糊查询的时候，会触发remoteMethod方法
  },
};
