<template >
  <div>
    <!-- 上方区域 -->
    <div>
      <el-menu class="el-menu-demo" mode="horizontal"></el-menu>
      <div class="line"></div>
      <el-menu
        class="el-menu-demo"
        mode="horizontal"
        background-color="#545c64"
        text-color="#fff"
      >
        <el-menu-item>教学服务平台</el-menu-item>
      </el-menu>
    </div>
    <!-- 左边区域 -->
    <el-container style="height: 660px; border: 1px solid #eee"
      ><!-- css不会调大小 -->
      <el-aside width="200px" style="background-color: rgb(238, 241, 246)">
        <el-menu :default-openeds="['1', '2', '3']" router>
          <el-submenu index="1">
            <template slot="title"
              ><i class="el-icon-realdate"></i>课程管理</template
            >
            <el-menu-item-group>
              <!-- 输入index标签就是跳转的路由，路由在router文件夹里的index.js里配置，index是数字的表示还没配置 -->
              <el-menu-item index="/Courses">课程</el-menu-item>
              <el-menu-item index="/Activities">活动</el-menu-item>
              <el-menu-item v-if="userid != 666" index="/Calendar"
                >课表</el-menu-item
              >
              <!-- <el-menu-item index="1-3">作业管理</el-menu-item> -->
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="2">
            <template slot="title"
              ><i class="el-icon-position"></i>校园导航</template
            >
            <el-menu-item-group>
              <el-menu-item index="/Map">校园导航</el-menu-item>
            </el-menu-item-group>
          </el-submenu>
          <el-submenu index="3">
            <template slot="title"
              ><i class="el-icon-setting"></i>闹钟</template
            >
            <el-menu-item-group>
              <el-menu-item index="/Clock">闹钟设置</el-menu-item>
            </el-menu-item-group>
          </el-submenu>

          <!-- 添加 -->
          <!-- <el-submenu index="4">
            <template slot="title"
              ><i class="el-icon-plus"></i>闹钟设置</template
            >
            <el-menu-item-group>
              
             <el-menu-item index="4">测试时间</el-menu-item>
            </el-menu-item-group>
          </el-submenu> -->
        </el-menu>
      </el-aside>

      <!-- 显示姓名时间那一行的区域 -->
      <el-container>
        <el-header style="text-align: right; font-size: 14px">
          <div>
            <!-- 下面两个div由isdebug（是否是调试模式）决定显示哪一个（v-if） -->
            <div style="float: left" v-if="!isdebug">
              <span>{{ timestr }}</span>
            </div>
            <div style="float: left" v-else>
              <el-button
                type="primary"
                icon="el-icon-video-pause"
                size="mini"
                @click="stop"
                >暂停</el-button
              ><el-button
                type="primary"
                icon="el-icon-check"
                size="mini"
                @click="backtime"
                >恢复</el-button
              >
              <span>{{ timestr }}</span>
              <el-button
                type="primary"
                icon="el-icon-caret-right"
                size="mini"
                @click="speedup"
                >快进</el-button
              >
            </div>

            <el-dropdown trigger="click" @command="handle">
              <i class="el-icon-setting" style="margin-right: 14px"></i>
              <el-dropdown-menu slot="dropdown">
                <el-dropdown-item command="a">{{ str }}</el-dropdown-item>
                <el-dropdown-item command="b">修改信息</el-dropdown-item>
                <el-dropdown-item command="c">退出</el-dropdown-item>
              </el-dropdown-menu>
            </el-dropdown>
            <span> {{ getname() }} </span>

            <el-dialog title="修改密码" :visible.sync="dialogFormVisible">
              <el-form>
                <el-form-item label="用户名" :label-width="formLabelWidth">
                  <el-input placeholder="用户名" v-model="userid"></el-input>
                </el-form-item>
                <el-form-item label="密码" :label-width="formLabelWidth">
                  <el-input
                    placeholder="密码"
                    v-model="password"
                    type="password"
                  ></el-input>
                </el-form-item>
                <el-form-item label="新密码" :label-width="formLabelWidth">
                  <el-input
                    placeholder="新密码"
                    v-model="newpassword"
                    type="password"
                  ></el-input>
                </el-form-item>
              </el-form>
              <div slot="footer" class="dialog-footer">
                <el-button
                  @click="
                    dialogFormVisible = false;
                    this.password = '';
                    this.newpassword = '';
                  "
                  >取 消</el-button
                >
                <el-button type="primary" @click="submit">确 定</el-button>
              </div>
            </el-dialog>
          </div>
        </el-header>

        <div>
          <keep-alive include="Clock">
            <router-view></router-view>
          </keep-alive>
        </div>
        <!-- enter -->
      </el-container>
    </el-container>
  </div>
</template>

<style>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  line-height: 60px;
}

.el-aside {
  color: #333;
}
</style>

<script>
import axios from "axios";
export default {
  data() {
    return {
      astr: "调试模式",
      bstr: "退出调试模式",
      realdate: new Date(), //初始化时间
      speeding: false, //是否处于快进状态，true表示不在快进状态
      stopping: false,
      isdebug: false, //是否处于调试模式
      staticdate: new Date(), //计算用起始时间初始化详情见下方speedup和mounted代码
      sdate: new Date(),
      modedate: new Date(),
      datestore: new Date(),
      tick: "",
      interv: 200,
      deinterv: 200,
      userid: "",
      void: "",

      password: "",
      newpassword: "",
      dialogTableVisible: false,
      dialogFormVisible: false,
      formLabelWidth: "120px",
    };
  },
  computed: {
    //展示快进的时间还是正常时间,！！！！！！！推荐用showtime来管理闹钟功能
    timestr: {
      get() {
        return this.showtime.toLocaleString().slice(0, -3) + this.void;
      },
    },
    showtime() {
      if (this.isdebug == true) return this.modedate;
      if (this.stopping == true) return this.datestore;
      else return this.realdate;
    },
    // 由isdebug决定显示哪一个字符串,astr还是bstr
    str() {
      if (this.isdebug == true) {
        return this.bstr;
      } else return this.astr;
    },
  },
  methods: {
    getname() {
      return JSON.parse(sessionStorage.getItem("username"));
    },
    handle(command) {
      console.log("click");
      switch (command) {
        case "a":
          this.isdebug = !this.isdebug;
          this.modedate = this.realdate;
          this.tick = setInterval(() => {
            this.modedate.setTime(this.modedate.getTime() + this.deinterv);
          }, this.interv);
          break;
        case "b":
          console.log("b");
          this.$confirm("修改密码，是否继续？", "提示", {
            iconClass: "el-icon-question",
            confirmButtonText: "确认",
            cancelButtonText: "取消",
            showClose: true,
            type: "warning",
          })
            .then(() => {
              this.dialogFormVisible = true;
            })
            .catch(() => {
              //删除
              this.$message({
                type: "info",
                message: "已取消",
              });
            });

          break;
        case "c":
          sessionStorage.clear();
          this.userid = "";
          this.$router.replace("Login");
          break;
      }
    },
    speedup() {
      // 点击时将目前的时间作为启示时间开始计算快进时间
      if (this.speeding === true) return;
      this.deinterv = 10 * this.deinterv;
      this.staticdate = this.modedate;
      clearInterval(this.tick);
      this.tick = setInterval(() => {
        this.modedate.setTime(this.modedate.getTime() + this.deinterv);
      }, this.interv);
    },
    stop() {
      this.deinterv = this.interv;
      this.stopping = true;
      this.speeding = false;
      this.datestore = this.showtime;
      clearInterval(this.tick);
    },
    backtime() {
      this.deinterv = this.interv;
      clearInterval(this.tick);
      this.modedate = this.realdate;
      this.tick = setInterval(() => {
        this.modedate.setTime(this.modedate.getTime() + this.deinterv);
      }, this.interv);
      this.speeding = false;
    },
    submit() {
      console.log("edit");
      if (this.userid == "" || this.password == "" || this.newpassword == "") {
        this.$message({ type: "primary", message: "请输入用户名(学号)或密码" });
        return;
      }
      let that = this;
      axios
        .post("http://localhost:3000/users/edit", {
          userid: that.userid,
          password: that.password,
          newpassword: that.newpassword,
        })
        .then((res) => {
          if (res.data.status === 1) {
            // sessionStorage.setItem(
            //   "user",
            //   JSON.stringify(parseInt(res.data.id))
            // );
            // sessionStorage.setItem("login", JSON.stringify(true));
            // sessionStorage.setItem("username", JSON.stringify(res.data.uname));
            // this.$bus.$emit("username")
            this.$message({ type: "success", message: "修改成功" });
            // this.$router.replace({ name: 'DisplayCourses' });
          } else this.$message({ type: "error", message: "用户名或密码错误" });
        })
        .catch((err) => {
          console.log(err);
          this.$message({ type: "primary", message: "网络错误" });
        });
      this.password = "";
      this.newpassword = "";
      this.dialogFormVisible = false;
    },
  },
  mounted() {
    this.userid = JSON.parse(sessionStorage.getItem("user"));
    this.$bus.$on("username", this.getname);
    // sessionStorage.setItem("user", JSON.stringify(2020211523));b
    console.log("mounted");
    // 每隔100ms更新时间和快进时间及其对应字符串
    // 逻辑是无论是否处于快进状态都在计算快进时间和正常时间，快进时间由现在时间和确定的起始时间的差乘以倍数后加上起始时间决定
    setInterval(() => {
      this.void = " ";
      this.void = "";
      this.realdate = new Date();
      this.$bus.$emit("time", this.showtime);
      this.$bus.$emit("maptime", this.showtime);

      // this.sdate.setTime(
      //   this.staticdate.getTime() +
      //     1800 * (this.realdate.getTime() - this.staticdate.getTime())
      // );
    }, this.interv);
  },
};
</script>