<template>
  <div>
    <!-- <el-button
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @click="back"
      size="mini"
      >返回</el-button
    > -->
    <el-page-header
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @back="back"
      content="添加课程"
    >
    </el-page-header>
    <el-descriptions
      style="margin-left: 5px; margin-top: 5px"
      :column="2"
      border
      height="400"
    >
      <el-descriptions-item label="课程名"
        ><el-col :span="8"><el-input v-model="course.name"></el-input></el-col>
      </el-descriptions-item>
      <el-descriptions-item label="id编号"> 添加后获得 </el-descriptions-item>
      <el-descriptions-item label="上课时间">
        <div
          v-for="(day, index) in course.week"
          :key="index"
          style="float: left; margin-left: 10px"
        >
          <el-checkbox size="mini" :label="day" v-model="course.week[index]">{{
            "周" + week[index]
          }}</el-checkbox>
        </div>
        <br />
        <div v-for="(day, index) in week" :key="index">
          <div v-show="course.week[index]">
            周{{ day }}
            <!-- <el-time-picker
              is-range
              size="small"
              v-model="course.time[index]"
              range-separator="至"
              start-placeholder="开始时间"
              end-placeholder="结束时间"
              placeholder="选择时间范围"
              format="HH:mm"
              value-format="HH:mm"
            ></el-time-picker> -->
            <el-select v-model="course.time[index][0]" placeholder="选择开始时间">
              <el-option
                v-for="item in options1"
                :key="item"
                :label="item"
                :value="item"
              >
              </el-option>
              </el-select>
              <el-select v-model="course.time[index][1]" placeholder="选择结束时间">
              <el-option
                v-for="item in options2"
                :key="item"
                :label="item"
                :value="item"
              >
              </el-option>
            </el-select>
          </div>
        </div>
      </el-descriptions-item>
      <el-descriptions-item span="2" label="班级">
        <el-col :span="10">
          <el-input
            size="small"
            type="number"
            v-model="course.classes"
          ></el-input>
        </el-col>
        <br />
      </el-descriptions-item>
      <el-descriptions-item label="地点">
        <el-dropdown @command="chooseplace">
          <span class="el-dropdown-link">
            {{ showplace }}<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="19">西土城主楼</el-dropdown-item>
            <el-dropdown-item command="20">西土城教1楼</el-dropdown-item>
            <el-dropdown-item command="21">西土城教2楼xx</el-dropdown-item>
            <el-dropdown-item command="22">西土城教3楼</el-dropdown-item>
            <el-dropdown-item command="23">西土城教4楼</el-dropdown-item>
            <el-dropdown-item command="128">沙河教学楼S1</el-dropdown-item>
            <el-dropdown-item command="129">沙河实验楼S2,S3</el-dropdown-item>
            <el-dropdown-item command="131"
              >沙河教学实验综合楼</el-dropdown-item
            >
          </el-dropdown-menu>
        </el-dropdown>
      </el-descriptions-item>
      <el-descriptions-item label="详细地点"
        ><el-col :span="8"
          ><el-input v-model="course.place[1]"></el-input></el-col
      ></el-descriptions-item>
      <el-descriptions-item label="授课老师"
        ><el-col :span="8"
          ><el-input v-model="course.teacher"></el-input></el-col
      ></el-descriptions-item>
      <el-descriptions-item label="课程信息"
        ><el-input type="textarea" autosize v-model="course.info[0]"></el-input
      ></el-descriptions-item>
      <el-descriptions-item label="考试信息"
        ><el-input type="textarea" autosize v-model="course.info[2]"></el-input
      ></el-descriptions-item>
      <el-descriptions-item label="作业信息"
        ><el-input type="textarea" autosize v-model="course.info[1]"></el-input
      ></el-descriptions-item>
      <!-- <el-descriptions-item label="作业信息">
        <div>（简介）</div>
        <el-divider></el-divider>
        <div>提交窗口</div></el-descriptions-item
      > -->
    </el-descriptions>
    <el-button
      type="success"
      style="margin-bottom: 5px; margin-left: 5px; margin-top: 5px; float: left"
      @click="add"
      size="mini"
      >添加</el-button
    >
  </div>
</template>
<script>
import axios from "axios";
export default {
  data() {
    return {
      sid: "",
      course: {
        name: "",
        id: 0,
        classes: "",
        // week: [true,false,false,true,false,false,false],
        week: [false, false, false, false, false, false, false],
        time: [
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
          ["00:00", "00:00"],
        ],
        place: [0, ""],
        teacher: "",
        info: ["", "", ""],
        url: "/course/0",
      },
      week: ["一", "二", "三", "四", "五", "六", "日"],
      placename: new Array(200),
      void: "",
      options1:["08:00","08:50","09:50","10:40","11:30","13:00","13:50","14:45","15:40","16:35","17:25","18:30","19:20","20:10"],
      options2:["08:45","09:35","10:35","11:25","12:15","13:45","14:35","15:30","16:25","17:20","18:10","19:15","20:05","20:55"]
    };
  },
  computed: {
    showplace: {
      get() {
        this.course.place[0];
        return this.placename[this.course.place[0]] + this.void;
      },
    },
  },
  methods: {
    chooseplace(command) {
      this.course.place[0] = command;
      this.void = " ";
      this.void = "";
      // this.showplace();
    },
    back() {
      this.$router.replace({ name: "DisplayCourses" });
    },
    add() {
      this.$confirm("添加新的课程, 是否确认?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          let that = this;
          that.course.place[0] = parseInt(that.course.place[0]);
          axios
            .post("http://localhost:3000/course/add", {
              sid: that.sid,
              course: that.course,
            })
            .then((res) => {
              if (res.data == "0")
                this.$message({
                  type: "error",
                  message: "没有权限!",
                });
              else if (res.data == "1")
                this.$message({
                  type: "success",
                  message: "添加成功!",
                });
            })
            .catch((err) => {
              console.log(err);
              this.$message({
                type: "info",
                message: "信息或格式有误，请修改",
              });
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消",
          });
        });

      this.$router.replace({ name: "DisplayCourses" });
    },
  },
  mounted() {
    this.placename[19] = "西土城主楼";
    this.placename[20] = "西土城教1楼";
    this.placename[21] = "西土城教2楼";
    this.placename[22] = "西土城教3楼";
    this.placename[23] = "西土城教4楼";
    this.placename[128] = "沙河教学楼";
    this.placename[129] = "沙河实验楼";
    this.placename[131] = "沙河教学实验楼";
    console.log(this.$route.params);
    this.sid = JSON.parse(sessionStorage.getItem("user"));
  },
};
</script>








<style scoped>
</style>